@import "../../../assets/styles/app";

.favoriteSingle {
  cursor: pointer;
  width: 32px;
  height: 32px;
  transition: 0.3s;
  outline: 3px solid transparent;

  &_canDrop {
    //box-shadow: 0 0 2px 2px rgba(255, 255, 255, 1);
    outline: 3px solid rgba(255, 255, 255, 1.0);
  }

  &_isOver {
    //box-shadow: 0 0 2px 2px green;
    outline: 3px solid green;
  }

  &_isDragging {
    //box-shadow: none;
    outline: none;
  }

  &:not(:first-child) {
    margin-top: 8px;
  }

  img {
    width: 32px;
    height: 32px;
  }
}