@import "../../../assets/styles/app";

.single {
  display: flex;
  align-items: center;
  text-decoration: none;
  transition: 0.3s;
  outline: 3px solid transparent;

  &_canDrop {
    //box-shadow: 0 0 2px 2px rgba(255, 255, 255, 1);
    outline: 3px solid rgba(255, 255, 255, 1.0);
  }

  &_isOver {
    //box-shadow: 0 0 2px 2px green;
    outline: 3px solid green;
  }

  &_isDragging {
    //box-shadow: none;
    outline: none;
  }

  &:not(:first-child) {
    margin-top: 8px;
  }

  img {
    display: block;
    width: 32px;
    height: 32px;
  }

  p {
    margin-left: 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    text-transform: uppercase;
    color: $color_body_50;
  }

}